@import "variables.scss";

.blog {
  margin: 0 auto;
  padding: 0 15px;
  margin-top: 95px;
  max-width: $s_container;
  display: flex;
  flex-direction: column;

  &__title {
    align-self: center;
    font-size: 45px;
    text-transform: uppercase;
    color: $main-black-color;
    font-weight: 800;
    letter-spacing: -1.2px;
    margin: 0;
  }

  &__subtitle {
    align-self: center;
    color: #717072;
    font-size: 17px;
    margin-top: 6px;
    letter-spacing: 0.25px;
  }

  &__news {
    margin-top: 57px;
    margin-right: -15px;
  }

  &__new {
    display: flex;
    overflow: hidden;
    margin-bottom: 40px;
  }

  &__new-card {
    width: 100%;
    max-width: 360px;
    margin: 0 auto 30px;
    display: flex;
    flex-direction: column;
  }
  
  &__new-img {
    position: relative;
    padding: 70% 0 0;
  }
  
  &__new-wrap {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: #717072;
  }
  
  &__new-img img {
    transition: all 0.6s ease;
  }
  
  &__new-card a:hover img{
    opacity: 0.5;
  }
  
  &__new-date {
    text-align: center;
    flex-shrink: 0;
    position: absolute;
    top: 0;
    left: 0;
    background-color: $main-black-color;
    width: 21%;
    padding: 18px 1px;
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
  
    span {
      color: #fff;
      font-size: 13px;
      font-weight: 700;
    }
  }
  
  &__new-title {
    display: flex;
    color: #242424;
    font-weight: 700;
    letter-spacing: 0.5px;
    margin-top: 19px;
  
    h3 {
      border-bottom: 3px solid transparent;
      transition: all 0.2s ease;
    }
  
    &:hover h3 {
      border-color: #242424;
    }
  }
  
  &__new-category {
    font-size: 12px;
    color: #242424;
    margin-top: 7px;
    font-weight: 500;
    letter-spacing: 0.4px;
  }
  
  &__new-text {
    margin-top: 20px;
    font-size: 12px;
    letter-spacing: -0.17px;
    line-height: 2;
  }
  
  &__new-link-more {
    margin-top: 21px;
    text-decoration: none;
    font-size: 14px;
    color: #929293;
    border: 1px solid #6a696b;
    border-radius: 30px;
    text-align: center;
    padding: 10px 10px;
    font-weight: 700;
    letter-spacing: 0.4px;
    transition: all 0.6s ease;
    align-self: flex-start;
  
    &:hover {
      color: #fff;
      background-color: #242424;
      box-shadow: 3px 3px 10px #242424;
      border-color: #242424;
    }
  }

  &__link-more {
    color: #6a696b;
    font-size: 14px;
    font-weight: 400;
    align-self: flex-end;
    letter-spacing: 0.6px;
    border-bottom: 1px solid transparent;
    transition: all 0.2s ease;

    &:hover {
      border-color: #6a696b;
    }
  }
}


@media (min-width: 990px) {
  .blog__new:nth-last-child(-n+3):nth-last-child(n+1) {
      margin-bottom: 0px;
  }
}

@media (min-width: 767px) {
  &__new:nth-last-child(-n+2):nth-last-child(n+1) {
    margin-bottom: 0px;
  }
}

@media (max-width: 767px) {
  .blog {
    margin-top: 35px;

    .blog__new:last-child {
      margin-bottom: 0px;
    }
  }
}

@media (max-width: 540px) {
  .blog__subtitle {
      text-align: center;
    }
}